.Popup{
  height: 100vh;
  width: 100vw;
  background-color: rgba(0,0,0,0.4);
  position: fixed;
  top: 0;
  bottom: 0; 
  display: flex;
  z-index: 50;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: .5s;
  &__content{
    position: relative;
    padding: 50px;
    border-radius: 20px;
    background-color: #1E1E1E;
    width: 50vh;
    @media (max-width:800px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 40vh;
      padding: 20px;
    }
    @media (max-width:400px) {
      img{
        display: none;
      }
    }
    h1{
      margin-top: 30px;
      text-align: center;
      @media (max-width:400px) {
        font-size: 24px;
        margin-top: 0;
      }
    }
    h3{
      margin-top: 30px;
    }
  }
 
  &.active{
    opacity: 1;
    pointer-events: all;
  }
   
}
.faCircleXmark{
  font-size: 40px;
  position: absolute;
  top: 10px;
  right: 10px;
  @media (max-width:400px) {
    font-size: 30px;
  }
}
.auth-form{
  margin-top: 20px;
  margin-bottom: 20px;
  color: black;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  @media (max-width:800px) {
    text-align: center;
  }
  @media (max-width:500px) {
    p{
      font-size: 14px;
    }
  }
  
  &__label{
    font-weight: bold;
    color: rgb(228, 186, 186);

  }
  &__input{
    cursor: pointer;
    color: black;
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 20px;
    padding: 10px;
    border-radius: 15px;
    border: none;
    @media (max-width:800px) {
      text-align: center;
    }
    @media (max-width:500px) {
      padding: 8px 0px;
      font-size: 14px;
      margin-bottom: 5px;
      
    }
  }
  &__input[name="checkbox"]{
    margin-left: 20px;
    transform: scale(2);
  }
  &__submit{
    background-color: red;
    margin-top: 40px;
    transition: .3s ease;
    &:hover{
      transition: .3s ease;
    background-color: rgb(143, 0, 0);
    color: white;
    }
    @media (max-width:400px) {
      margin-top: 10px;
    }
  }
}
.registration{
  margin-left: 10px;
  font-size: 18px;
  cursor: pointer;
  border: none;
  color: black;
  padding: 5px;
  border-radius: 10px;
}
