.Header{
  background-color: black;
  &__inner{
    display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  @media (max-width:600px) {
    flex-direction: column;
    row-gap: 20px;
    }
  }
}
.Auth{
  display: flex;
  column-gap: 20px;
  &__user{
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 20px;
  }
  &__user-name{
    display: flex;
    flex-direction: column;
    align-items: end;
    @media (max-width:500px) {
      h2{
      font-size: 14px;

      }
    }
  }
}
.Button{
  cursor: pointer;
  font-size: 25px;
  padding: 5px 20px;
  background-color: red;
  border: 1px solid black;
  border-radius: 10px;
  transition: .3s ease;
  @media (max-width:600px) {
    font-size: 20px;
  }
  &:hover{
    transition: .3s ease;
    background-color: rgb(143, 0, 0);
    color: white;
  }
}
.faCircleUser{
  font-size: 50px;
}
.faDoorOpen{
  font-size: 20px;
}
