.Main{
  &__inner{
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(5,1fr);
  column-gap: 20px;
  row-gap: 10px;
  @media (max-width:1100px) {
      grid-template-columns: repeat(4,1fr);
      column-gap: 20px;
      row-gap: 10px;
    }
  @media (max-width:900px) {
      grid-template-columns: repeat(3,1fr);
      column-gap: 20px;
      row-gap: 10px;
    }
  @media (max-width:700px) {
      grid-template-columns: repeat(2,1fr);
      column-gap: 20px;
      row-gap: 10px;
    }
  @media (max-width:500px) {
      grid-template-columns: 1fr;
      column-gap: 20px;
      row-gap: 10px;
    }
  }
}
.GameCard{
  position: relative;
  display: flex;
  row-gap: 5px;
  flex-direction: column;
  align-items: center;
    transition: .3s ease;
    &:hover{
      transition: .3s ease;
      transform: scale(1.1);
  }
  @media (max-width:500px) {
    padding-bottom: 10px;
    border-bottom: 5px solid red;
  }
}
.faCircleQuestion{
  position: absolute;
  top: 5%;
  right: 5%;
  z-index: 10;
  font-size: 25px;
  background-color: rgb(104, 65, 65);
  border-radius: 50%;
}
.promo{
  border-radius: 10px;
  width: 100%;
 
}
.rating{
  position: absolute;
  bottom: 20%;
  left: 5%;
  z-index: 20;
  padding: 2px 5px;
  font-size: 15px;
  border-radius: 10px;
  background-color: rgb(0, 0, 0);
}
.rating-amount{
  margin-left: 5px;
}
.search-form{
  position: relative;
  display: block;
  height: 100%;
  
  &__input{
    cursor: pointer;
    font-size: 25px;
    padding: 10px 20px;
    border-radius: 15px;
    border: none;
    background-color: red;
    &::placeholder{
      color: rgb(228, 186, 186);
    }
    @media (max-width:600px) {
      font-size: 20px;
    }
  }
}
.faMagnifyingGlass{
  position: absolute;
  font-size: 20px;
  right: 0;
  top: 50%;
  transform:translate(-50%, -50%);
}
iframe 
{
  margin-top: 50px;
  display: block; 
  width: 100%; 
 
}